/**
* Layout in Large state
*/
@media #{$mediaLarge} {
  // visibility classes
  .hide-large {
    display: none;
  }

  .visible-large {
    display: block;
  }

  span.visible-large {
    display: inline;
  }

  .sidebar {
    max-width: $sidebarWidthLarge !important;
    min-width: $sidebarWidthLarge !important;
    will-change: inherit;
    position: static;
    height: auto;
    transform: none;
    transition: none;
    z-index: 0;
  }

  .subnav, .tabs {
    li {
      margin-right: 6px;
    }
  }

  .home {
    &:not(.single-column) {
      width: $sidebarWidthLarge;
    }
  }

  .header {
    padding: 0 $overallPaddingLarge 0 $overallPaddingLarge;
  }

  .content-wrapper {
    padding: 0 $overallPaddingLarge 0 $overallPaddingLarge;
  }

  .wrapper-full-width {
    .header {
      padding: 0 $overallPaddingLarge + $paddingRightLeftDesktop 0 $overallPaddingLarge
        + $paddingRightLeftDesktop;

      .navbar-main {
        padding: 0;

        .logo {
          margin-left: 0;
        }
      }
    }
  }
}